.counter {
    font: 900 45px/45px "Roboto", Helvetica, Arial, sans-serif;
    margin-bottom: 0;
    color: #fff;
}

.counter-bold {
    font-weight: 700;
}

.counter-k:after {
    content: 'k';
}

*+.counter-title {
    margin-top: 0;
}

.countdown-default {
    color: #000;
}

.countdown-default .countdown-section {
    position: relative;
    display: inline-block;
    min-width: 90px;
    padding: 0 10px;
    text-align: center;
}

.countdown-default .countdown-section>* {
    display: block;
}

.countdown-default .countdown-section:after {
    position: absolute;
    top: 35%;
    transform: translateY(-35%);
    border-radius: 20px;
    background: #000;
}

.countdown-default .countdown-section:nth-last-child(n+3):after {
    content: '';
    right: -2px;
    width: 5px;
    height: 5px;
}

@media (max-width: 767px) {
    .countdown-default .countdown-section:last-child {
        display: none;
    }
}

.countdown-default .countdown-amount {
    font-family: Helvetica, Arial, sans-serif;
    font-size: 30px;
    font-weight: 900;
    line-height: 1;
}

.countdown-default .countdown-period {
    margin-top: 10px;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: -.025em;
    color: rgba(0, 0, 0, 0.4);
}

.countdown-default.countdown-inverse .countdown-section:after {
    background: #fff;
}

.countdown-default.countdown-inverse .countdown-period {
    color: rgba(255, 255, 255, 0.4);
}

@media (min-width: 768px) {
    .countdown-default .countdown-section {
        min-width: 150px;
    }
    .countdown-default .countdown-section:not(:last-child):after {
        content: '';
        top: 50%;
        right: -5px;
        transform: translateY(-50%);
        width: 10px;
        height: 10px;
    }
    .countdown-default .countdown-amount {
        font-size: 50px;
    }
    .countdown-default .countdown-period {
        font-size: 14px;
    }
}

@media (min-width: 992px) {
    .countdown-default .countdown-section {
        min-width: 200px;
    }
    .countdown-default .countdown-amount {
        font-size: 72px;
    }
}

.countdown-inverse {
    color: #fff;
}


/*
* @subsection   Isotope
*/

[data-isotope-layout] {
    position: relative;
    display: block;
    transition: .4s all ease;
    min-height: 160px;
    width: 100%;
}

[data-isotope-layout]:after {
    content: '';
    position: absolute;
    width: 64px;
    height: 64px;
    top: 50%;
    left: 50%;
    background-image: url("data:image/png;base64,");
    background-position: -1152px 0;
    animation: 0.7s sprite-animation steps(18) infinite;
    transition: .4s all ease;
    transform: translate(-50%, -50%);
}

[data-isotope-layout] [class*="col-"] {
    display: block;
    opacity: 0;
    transition: .4s opacity ease;
    overflow: hidden;
    will-change: opacity, transform;
}

[data-isotope-layout].isotope--loaded [class*="col-"] {
    opacity: 1;
}

[data-isotope-layout].isotope--loaded:after {
    opacity: 0;
    visibility: hidden;
}

.isotope-gutter-default {
    position: relative;
    transform: translateY(-30px);
    margin-bottom: -30px;
}

.isotope-gutter-default [class*="col-"] {
    margin-top: 30px;
}

.isotope-gutter-default:after {
    margin-top: 15px;
}

.isotope-filters>* {
    margin-top: 0;
    vertical-align: middle;
}

.isotope-filters .list-inline {
    position: relative;
    transform: translateY(-10px);
    margin: 0;
    margin-bottom: -10px;
}

.isotope-filters .list-inline li {
    display: inline-block;
    margin-top: 10px;
}

.isotope-filters .list-inline a {
    position: relative;
    transition: .3s;
}

@media (min-width: 992px) {
    .isotope-filters .isotope-filters-trigger {
        display: none;
    }
}

.isotope-filters-minimal .list-inline>li {
    padding: 0;
}

@media (min-width: 992px) {
    .isotope-filters-minimal .list-inline {
        word-spacing: 0;
    }
    .isotope-filters-minimal .list-inline>li:not(:last-child) {
        margin-right: 16px;
    }
    .isotope-filters-minimal .list-inline a {
        position: relative;
        padding-bottom: 5px;
        font: 700 14px/24px "Roboto", Helvetica, Arial, sans-serif;
    }
    .isotope-filters-minimal .list-inline a:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 100%;
        height: 2px;
        transition: .33s all ease;
        background: #6bc3f0;
    }
    .isotope-filters-minimal .list-inline a,
    .isotope-filters-minimal .list-inline a:active,
    .isotope-filters-minimal .list-inline a:focus {
        color: #000;
    }
    .isotope-filters-minimal .list-inline a.active:hover {
        color: #000;
    }
    .isotope-filters-minimal .list-inline a:hover {
        color: #6bc3f0;
    }
    .isotope-filters-minimal .list-inline a.active:after {
        right: 0;
    }
}

.isotope-filters-responsive {
    position: relative;
    z-index: 10;
}

.isotope-filters-responsive *:focus {
    outline: none;
}

@media (max-width: 767px) {
    .isotope-filters-responsive {
        max-width: 370px;
        margin-left: auto;
        margin-right: auto;
    }
}

@media (max-width: 991px) {
    .isotope-filters-responsive {
